<template id="problem">
    <div class="problem_box">

        <div class="problem">
            <span class="protit">1.?</span>
            <p class="protex">!,</p>
        </div>
        <div class="problem">
            <span class="protit">2.?</span>
            <p class="protex"></p>
        </div>
        <div class="problem">
            <span class="protit">3.?</span>
            <p class="protex">1</p>
        </div>
    </div>
</template>
<style>
    .problem_box{
        background: #ffffff;
        height:100%;
    }
    .problem{
        padding:20px 15px 0 15px;
    }
    .protit{
        color:#333333;
        font-size: 16px;
    }
    .protex{
        color:#666666;
        font-size: 14px;
        margin-top:5px;
    }
</style>
<script type="text/javascript">

    var problem={
        template:'#problem'
    }
    module.exports=problem
</script>





